/* ==========BASIC UNIVERSAL STYLING & CLEAN UP============= */
    /* some basic css resetting */
body { margin: 0; box-sizing: border-box; }
a { text-decoration: none; color:black; }
/*============================Fonts==========================*/
    /* ========== Text Stylings ==========*/
        span {
            font-family: "Exo2Black";
            color: #3b3010;
            font-family: 'Exo 2', sans-serif;
        }

        h1, h2, h3, h4, h5, h6, cite {
            font-family: "Lemonada";
            text-align: center; 
            margin-top: 1%;
            margin-bottom: 0.25%;   
        }

        .segment h2{
            background-color: #f7ce76;
        }

        h2, h2 span, h3 span, h4 span, h5 span, h3, h4 {
            font-size: 3.2rem;
        }

        p, a {
            font-family: 'Forum', cursive;
        }
    /* ========== h1 = Header ============== */
        h1 {
            font-size: 5rem;
            text-shadow:5px 5px white;           
        }

        h1 span{
            font-size: 4.8rem;
        }

    /* ============ h2 = Segment Headings ========== */
        h2 {
            text-shadow:2px 2px white;
        }

        .writeup h2{
            background-color: initial;
        }

    /* ============h4 = Filter buttons  ========== */
        a {
            font-size: 1.25rem;
            font-weight: bolder;
        }
        a:hover{
            color: #688FB3;
        }
    
    /* ============ Paragraph ============ */
        p  {
            margin-bottom: 2px;  
        }
        h5 {
            font-size: 1.5rem;
        }
        

/* ==========HEADER============= */
html {
    scroll-behavior: smooth;
}

.clearfix::after {
    content: "";
    clear: both;
    display: table;
}
header {
    margin: 0;
    width: 100vw;
    height: 20vh;
    position: relative;
    background-color: rgb(116, 131, 145);
}

/* ==========LOGO============= */
/* ==========NAVIGATION============= */
/* Sticky Navigation Bar by Tutorial Republic
https://www.tutorialrepublic.com/faq/how-to-create-fixed-header-or-f-using-css.php*/
label, #burger {
    display: none; 
}
  
 
label {
    display: block; 
    padding: 10px;
}

  
nav input:checked ~ a { 
    display: block;
}

.topnav  {    
    position: sticky; 
    top:0;
    z-index: 200;
    width: 100vw;
    background-color:#fbd88f;
}
.navcontent {
    width: 60vw;
    margin: 0 20vw;
    padding: 0vw;
    top: 0;
}
nav {
    background-color: #fbd88f;
}
/*Button for Website Navigation*/
nav a {
 display: inline-block;
 text-align: center;
 padding: 1% 4%;
 text-decoration: none;   
}

nav a:hover{
    background-color: #FDB7B3;
    color: black;
}

a:hover {
    color: #688FB3;
}
/* ==========MAIN BODY CONTENT============= */
.content {
    width: 100vw;
    height:100%;
    border-top: solid #fcfcfc 1px;
    background-color: rgb(224, 224, 224);
  }
  * {
    box-sizing: border-box;
  }


/* ============= Announcement Space =============== */

/*
PURE RESPONSIVE CSS3 SLIDESHOW GALLERY by Roko C. buljan
https://codepen.io/rokobuljan/pen/XXzqKQ
*/

.CSSgal {
	position: relative;
	overflow: hidden;
	height: 100%; /* Or set a fixed height */
}

/* SLIDER */

.CSSgal .slider {
	
	white-space: nowrap;
	font-size: 0;
	transition: 0.8s;
}
.info {
    height: 100%;
    width: 60vw;
    padding: 5%;
    color: black;
}
/* SLIDES */

.CSSgal .slider > * {
	font-size: 1rem;
	display: inline-block;
	white-space: normal;
	vertical-align: top;
	width: 100%;
	background: none 50% no-repeat;
	background-size: cover;
}

/* PREV/NEXT, CONTAINERS & ANCHORS */

.CSSgal .prevNext {
	position: absolute;
	z-index: 2;
    top: 40%;
    bottom: 40%;
	width: 100%;
	height: 0;
}

.CSSgal .prevNext > div+div {
	visibility: hidden; /* Hide all but first P/N container */
}

.CSSgal .prevNext a {
	background: #fff;
	position: absolute;
	width:       60px;
	height:      60px;
	line-height: 60px; /* If you want to place numbers */
	text-align: center;
	opacity: 0.7;
	-webkit-transition: 0.3s;
					transition: 0.3s;
	-webkit-transform: translateY(-50%);
					transform: translateY(-50%);
	left: 0;
}
.CSSgal .prevNext a:hover {
	opacity: 1;
}
.CSSgal .prevNext a+a {
	left: auto;
	right: 0;
}

/* NAVIGATION */

.CSSgal .bullets {
	position: relative;
	z-index: 2;
	bottom: 0;
	padding: 10px 0;
	width: 100%;
    text-align: center;
    transform: translateY(-110%);
}
.CSSgal .bullets > a {
	display: inline-block;
	width:       15px;
	height:      15px;
	line-height: 15px;
	text-decoration: none;
	text-align: center;
	background: rgba(255, 255, 255, 1);
	-webkit-transition: 0.3s;
        transition: 0.3s;
    margin: .5vw;
    
}
.CSSgal .bullets > a+a {
	background: rgba(255, 255, 255, 0.5); /* Dim all but first */
}
.CSSgal .bullets > a:hover {
	background: rgba(255, 255, 255, 0.7) !important;
}

/* NAVIGATION BUTTONS */
/* ALL: */
.CSSgal >s:target ~ .bullets >* {      background: rgba(255, 255, 255, 0.5);}
/* ACTIVE */
#s1:target ~ .bullets >*:nth-child(1) {background: rgba(255, 255, 255,   1);}
#s2:target ~ .bullets >*:nth-child(2) {background: rgba(255, 255, 255,   1);}
#s3:target ~ .bullets >*:nth-child(3) {background: rgba(255, 255, 255,   1);}
#s4:target ~ .bullets >*:nth-child(4) {background: rgba(255, 255, 255,   1);}
#s5:target ~ .bullets >*:nth-child(1) {background: rgba(255, 255, 255,   1);}
#s6:target ~ .bullets >*:nth-child(2) {background: rgba(255, 255, 255,   1);}
#s7:target ~ .bullets >*:nth-child(1) {background: rgba(255, 255, 255,   1);}
#s8:target ~ .bullets >*:nth-child(2) {background: rgba(255, 255, 255,   1);}
#s9:target ~ .bullets >*:nth-child(3) {background: rgba(255, 255, 255,   1);}
#s10:target ~ .bullets >*:nth-child(4) {background: rgba(255, 255, 255,   1);}
#s11:target ~ .bullets >*:nth-child(1) {background: rgba(255, 255, 255,   1);}
#s12:target ~ .bullets >*:nth-child(2) {background: rgba(255, 255, 255,   1);}
#s13:target ~ .bullets >*:nth-child(3) {background: rgba(255, 255, 255,   1);}
#s14:target ~ .bullets >*:nth-child(4) {background: rgba(255, 255, 255,   1);}

