/* ==========SECTION 1 STYLING============= */
label, #burger {
    display: none; 
}

article.mainblock {
    position: relative;
    top: 10%;
    display: block;
    width: 95%;
    margin: 3% 2.5% 1.25%;
}
.writeup {
    display: flex;
    width: 100%;
    text-align: center;
}

.info .writeup{
    display: inline-block;
}

article.mainblock:after {
    content: "";
    display: table;
    clear: both;
}

a.filter {
    font-family: "Lemonada";
    text-align: center;
    color: white;
}
.centered {
    position: relative;
    width: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -150%);
}

centered::after {
    clear: both;
    content: "";
    display: initial;
}

#intro .frame::after{
    clear: both;
    content: "";
    display: initial;
    margin: initial;
}

#intro .frame a img {
    filter: brightness(40.5%);
    -webkit-filter: brightness(40.5%);
}

/* ==========SECTION 2 STYLING============= */
.museum {
    position: relative;
    margin:  0% 2%;
    padding: 0;
    width: 96%;
}

.museum::part(nav){
    width: 100%;
    background-color: #fbd88f;

}

.museum::part(active-tab-indicator){
    --indicator-color: #794442;
}

/*Gallery Layout by W3 Schools - Mixed Layout
https://www.w3schools.com/howto/howto_css_mixed_columns.asp
*/
.frame {
    float: left;
    width: 24%;
    margin-right: 1%;
    text-align: center;
}


/* ==========SECTION 3 STYLING============= */
.paragraph p {
    font-size: inherit;
}

.writeup h3 {
    margin: 0;
}

.writeup h5 {
    margin-bottom: 1%;
}

.writeup div{
    padding-left: 5%;
    padding-right: 5%;
}

/* ==========SECTION 4 STYLING============= */

.tablink::part(base), .tabfilter::part(base){
    background-color: #fbd88f;
    color: black;
    float: left;
    cursor: pointer;
    font-size: 1.5rem;
    font-family: "Forum";
    font-weight: bolder;
    --padding: 1% 0;
}
  
  /* Style the tab content */
.tabcontent {
    --padding: 2% ;
    text-align: left;
    width: 100%;
}


  
#Asia, #Africa, #Europe, #Carribean, #Americas, .museum::part(body) {background-color:white;}

